<template>
    <div class="mapForAddress" title="确认收货地址" style="height: 100%;width:100%;display: flex;flex-direction: column;">
        <navigation-bar class="titleBar"  @onLeftClick="onLeftClick" :pageName="title" :bothWidth="headerItemWidth" ></navigation-bar>
        <div class="searchBar" :style="{marginTop:this.$stbHeight+50+'px'}">
            <div class="cityBtnDiv" @click="changeCity">
                <div class="cityBtn">{{currentCity}}</div>
                <img src="../../assets/images/common/home_cutover_black.png">
            </div>
            <van-form action="">
                <van-search
                        id="tipinput"
                        type="search"
                        v-model="inputAddress"
                        placeholder="小区/写字楼/学校等"
                        background="#F7F7F7"
                        shape="round"
                        @input="tipInputting"
                        @click="gotoSearch"
                        style="float: right;padding:0;width: 70%;margin-right: 4%;margin-top: 1%"
                >
                    <div slot="left-icon">
                        <img class="searchIcon" src="../../assets/images/mall/search_black.png">
                    </div>
                </van-search>
            </van-form>
        </div>
        <div>
            <div v-show="!isNetworkError && !isShowSearch">
                <div class="mapDiv">
                    <template>
                        <div class="map">
                            <div id="GDMap" style="height:7rem">
                                <div class="shop-content-marker"></div>
                            </div>
                        </div>
                    </template>
                </div>
                <div class="addressListDiv" style="flex: 1" ref="addressListDiv">
                    <div class="address-list" :style="{height:addressListHeight+'px'}" id="addressListDiv">
                        <ul>
                            <li v-for="(item,i) in addressData" @click="goBack(item)" v-if="addressData">
                                <i class="node-icon">
                                    <img class="pointImg" src="../../assets/images/mall/gpsAddress.png" v-if="i == 0" />
                                    <img class="rollImg" src="../../assets/images/mall/gpsAddviceAddress.png" v-else/>
                                </i>
                                <div class="addressRightInfo">
                                    <div>
                                        <div id="name" class="addressName" style="color:#FB5768" v-if="i ==0">{{item.name}}</div>
                                        <div class="addressName" style="color: #333333" v-else>{{item.name}}</div>
                                    </div>
                                    <div>
                                        <div id="district" class="addressDetail" v-if="item.address.length !=0">{{item.address}}</div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div v-show="!isNetworkError && isShowSearch" >
                <div class="advice-list" ref="advicelistDiv" :style="{height: addressadvicesHeight+'px'}">
                    <ul>
                        <li v-for="(item,i) in adviceAddress" @click="goBack(item)" v-if="item.location">
                            <div class="rightInfo">
                                <div>
                                    <div class="addressName">{{item.name}}</div>
                                </div>
                                <div>
                                    <div class="addressDetail" v-if="item.address.length !=0">{{item.address}}</div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div style="padding-top:100px;height:100%" v-if="isNetworkError">
                <NetworkError subTitle="" customBg="transparent"></NetworkError>
            </div>
        </div>

    </div>
</template>

<script>
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import NetworkError from '@/common/networkError/NetworkError';
    import loadMap from '../../components/AMapLoader/loadMap';
    import { store } from "../../utils/store";
    import { Toast } from "vant";
    import remoteLoad from "../../components/AMapLoader/remoteLoad";
    export default {
        name: "mapForAddress.vue",
        components:{
            NavigationBar,
            NetworkError,
        },
        directives: {
            focus: {
                update: function (el, {value}) {
                    if (value) {
                        el.focus()
                    }
                }
            }
        },
        data(){
            return {
                title:'确认收货地址',
                headerItemWidth:{
                    width:'84px'
                },
                isShowSearch:false,//是否显示搜索界面
                searchKey:"", //搜索的内容
                focusState:true, //焦点状态
                inputAddress:'',
                setAddressInput:'',
                expressMap:null,//地图实例
                autoComplete:null,//地址输入联想
                geoLocation:null,//高德定位
                geoCoder:null,//高德地理编码插件g
                currentCity:'全国',
                district:'',
                province:'',
                address:'',
                mapAdress:'',
                isNetworkError:false,
                isBackSocketError:false,
                adviceAddress:[], //自动补全结果
                addressData:[],//poi联想结果
                screeHeight: document.body.clientHeight,
                addressListHeight:'',
                addressadvicesHeight:'',
                setTopHeight:{
                    'padding-top':this.$stbHeight+'px'
                },
                pointAddressData:{
                    address:'',
                },
                AMap:null,
                thisPosition: {
                    location: '',
                    lng: '',
                    lat: ''
                },
                currentPosition: {
                    lng: '',
                    lat: ''
                },
                setAddressData:{},
            }
        },
        created() {
        },
        activated(){
            if (this.$route.query.currentCity){
                this.currentCity=this.$route.query.currentCity;
                this.autoComplete.setCity(this.currentCity);
            }
            this.$nextTick(()=>{
                this.addressListHeight=this.screeHeight-this.$refs.addressListDiv.offsetTop-10;
                this.addressadvicesHeight=this.screeHeight-this.$refs.advicelistDiv.offsetTop;
            })
        },
        beforeRouteEnter(to,form,next){
            console.log(to);
            if (form.fullPath !== "/choseCityForAddress"){
                next(vm => {
                    vm.isNetworkError=false;
                    if (vm.$route.params.recAddr){
                        vm.mapAdress=vm.$route.params.recAddr;
                    }
                    if (vm.$route.params.recCity){
                        vm.currentCity=vm.$route.params.recCity;
                    }
                    vm.mapinit();
                })
            }else {
                next();
            }
        },
        methods:{
            onLeftClick(){
                if(this.isShowSearch){
                    this.isShowSearch =false;
                }else {
                    this.inputAddress='';
                    this.$router.go(-1);
                }
            },
            //返回上一层路由
            goBack(addressItem){
                console.log(addressItem);
                let that = this;
                this.AMap.plugin('AMap.Geocoder', function() {
                    var geocoder = new AMap.Geocoder({
                        // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
                        city: that.currentCity,
                    })
                    var lnglat = [addressItem.location.lng,addressItem.location.lat]
                    geocoder.getAddress(lnglat, function(status, result) {
                        if (status === 'complete' && result.info === 'OK') {
                            // result为对应的地理位置详细信息
                            console.log(result);
                            let data={
                                areaLatitude:addressItem.location.lat,
                                areaLongitude:addressItem.location.lng,
                                recAreaName:addressItem.address+addressItem.name,
                                recCity: result.regeocode.addressComponent.city==""?result.regeocode.addressComponent.province:result.regeocode.addressComponent.city,
                                recDistrict: result.regeocode.addressComponent.district,
                                recProvince: result.regeocode.addressComponent.province,
                            };
                            store.get("updatePageData")(data);
                            that.isShowSearch =false;
                            that.mapAdress='';
                            that.inputAddress='';
                            that.$router.go(-1);
                        }else {
                            //查询错误
                            this.$toast("查询功能异常，请稍后重试");
                        }
                    },function () {
                        this.$toast("网络异常，请稍后重试");
                    })
                })
            },
            //搜索输入框获取焦点
            searchInputFocus(e){
                var obj = e.srcElement;
                console.log("【输入框】searchInputFocus "+obj.value.length)
                var value = obj.value;
                obj.value = ""
                obj.value = value
            },
            tipInputting(val){
                let that = this;
                console.log(val);
                this.autoComplete.search(val, function(status, result) {
                    // 搜索成功时，result即是对应的匹配数据
                    console.log(result);
                    that.adviceAddress=result.tips;
                })
            },
            gotoSearch(){
                this.autoComplete.setCity(this.currentCity);
                this.isShowSearch=true;
            },
            mapinit(){
                let that = this;
                // if (this.AMap ==null){
                    loadMap()
                        .then(AMap => {
                            that.AMap = new AMap.Map('GDMap',{
                                zoom: 10,
                                center:[120.382689,36.066971],
                                animateEnable:true,
                                resizeEnable:true,
                            });
                            AMap.plugin('AMap.Autocomplete', function(){
                                var autoOptions = {
                                    city: that.currentCity
                                }
                                that.autoComplete = new AMap.Autocomplete(autoOptions);
                            })
                            if (that.mapAdress ==''){
                                that.getCurrentLocation();
                            }else {
                                that.getAddressPosition(that.mapAdress);
                            }
                        })
                        .catch((err) => {
                            this.isNetworkError=true;
                            console.log('地图加载失败 '+err)
                        })
            },
            mapMarker(longitute,latitude){ //指针
                let that = this;
                //加载PositionPicker，loadUI的路径参数为模块名中 'ui/' 之后的部分
                if (window.AMapUI){
                    AMapUI.loadUI(['misc/PositionPicker'], function(PositionPicker) {
                        console.log("111");
                        var map = new AMap.Map('GDMap',{
                            zoom:16
                        });
                        map.setCenter(new AMap.LngLat(longitute,latitude));
                        var positionPicker = new PositionPicker({
                            mode:'dragMap',//设定为拖拽地图模式，可选'dragMap'、'dragMarker'，默认为'dragMap'
                            map:map,//依赖地图对象
                            iconStyle: { //自定义外观
                                url: require("../../assets/images/mall/gpsMarkicon.png"),
                                ancher: [0, 0],
                                size: [14, 25]
                            },
                        });
                        //TODO:事件绑定、结果处理等
                        positionPicker.on('success', function(positionResult) {
                            console.log("positionResult",positionResult)
                            that.thisPosition.location = positionResult.address;
                            that.thisPosition.lng = positionResult.position.lng;
                            that.thisPosition.lat = positionResult.position.lat;
                            that.addressData=positionResult.regeocode.pois;
                            that.currentCity=positionResult.regeocode.addressComponent.city==""?positionResult.regeocode.addressComponent.province:positionResult.regeocode.addressComponent.city;
                            that.province=positionResult.regeocode.addressComponent.province;
                            that.district=positionResult.regeocode.addressComponent.district;
                        });
                        positionPicker.on('fail', function(positionResult) {
                            // 海上或海外无法获得地址信息
                            Toast('定位失败，请检查定位设置');
                            console.log(positionResult)
                        });
                        positionPicker.start();
                        map.panBy(0, 1);
                    });
                }else {
                    Promise.all([remoteLoad('https://webapi.amap.com/ui/1.0/main.js')]).then(()=>{
                        AMapUI.loadUI(['misc/PositionPicker'], function(PositionPicker) {
                            console.log("111");
                            var map = new AMap.Map('GDMap',{
                                zoom:16
                            });
                            map.setCenter(new AMap.LngLat(longitute,latitude));
                            var positionPicker = new PositionPicker({
                                mode:'dragMap',//设定为拖拽地图模式，可选'dragMap'、'dragMarker'，默认为'dragMap'
                                map:map,//依赖地图对象
                                iconStyle: { //自定义外观
                                    url: require("../../assets/images/mall/gpsMarkicon.png"),
                                    ancher: [0, 0],
                                    size: [14, 25]
                                },
                            });
                            //TODO:事件绑定、结果处理等
                            positionPicker.on('success', function(positionResult) {
                                console.log("positionResult",positionResult)
                                that.thisPosition.location = positionResult.address;
                                that.thisPosition.lng = positionResult.position.lng;
                                that.thisPosition.lat = positionResult.position.lat;
                                that.addressData=positionResult.regeocode.pois;
                                that.currentCity=positionResult.regeocode.addressComponent.city==""?positionResult.regeocode.addressComponent.province:positionResult.regeocode.addressComponent.city;
                                that.province=positionResult.regeocode.addressComponent.province;
                                that.district=positionResult.regeocode.addressComponent.district;
                            });
                            positionPicker.on('fail', function(positionResult) {
                                // 海上或海外无法获得地址信息
                                Toast('定位失败，请检查定位设置');
                                console.log(positionResult)
                            });
                            positionPicker.start();
                            map.panBy(0, 1);
                        });
                    })
                }
            },
            changeCity(){
                // this.autoComplete.setCity(this.currentCity);
                this.$router.push({ name: "choseCityForAddress",params:{city:this.currentCity}});
            },
            getCurrentLocation() {
                let that = this;
                this.AMap.plugin('AMap.Geolocation', function () {
                    var geolocation = new AMap.Geolocation({
                        enableHighAccuracy: true,
                        buttonOffset: new AMap.Pixel(10, 20),
                        showCircle:false,
                        showButton:false,
                        zoomToAccuracy: true,
                    });
                    that.AMap.addControl(geolocation);
                    geolocation.getCurrentPosition();
                    AMap.event.addListener(geolocation, 'complete', onComplete);
                    AMap.event.addListener(geolocation, 'error', onError);
                    function onComplete (data) {
                        // data是具体的定位信息
                        console.log(data)
                        if (data){
                            that.currentPosition.lng=data.position.lng;
                            that.currentPosition.lat=data.position.lat;
                            if (data.addressComponent){
                                that.currentCity=data.addressComponent.city;
                            }
                            that.mapMarker(data.position.lng,data.position.lat);
                        }
                    }
                    function onError (data) {
                        // 定位出错
                        console.log('定位失败错误：', data)
                        if (data.message=='Geolocation permission denied.'){
                            that.$toast('获取定位失败，请检查权限设置');
                        }else {
                            that.$toast("获取定位失败");
                        }
                        // that.mapMarker('120.51875','36.196667');
                    }
                })
            },
            decoderPosition(lng,lat){
                let that=this;
                this.AMap.plugin('AMap.Geocoder', function() {
                    var geocoder = new AMap.Geocoder({
                        // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
                        city: that.currentCity,
                    })
                    var lnglat = [lng, lat]
                    geocoder.getAddress(lnglat, function(status, result) {
                        if (status === 'complete' && result.info === 'OK') {
                            // result为对应的地理位置详细信息
                            console.log(result);
                        }
                    })
                })
            },
            getAddressPosition(address){
                let that=this;
                this.AMap.plugin('AMap.Geocoder', function() {
                    var geocoder = new AMap.Geocoder({
                        // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
                        city: that.currentCity,
                    })
                    geocoder.getLocation(address, function(status, result) {
                        if (status === 'complete' && result.info === 'OK') {
                            // result为对应的地理位置详细信息
                            console.log(result);
                            that.mapMarker(result.geocodes[0].location.lng,result.geocodes[0].location.lat)
                        }
                    })
                })
            },
        }
    }
</script>

<style>
    .amap-copyright {
        color: #8a8a8a;
        bottom: -3px;
    }
    .amap-logo {
        bottom: 1px;
        height: 20px;
        left: 1px;
        line-height: 20px;
    }
</style>
<style scoped lang="scss">
    .FixedsearchInput{
        width: 100%;
        border: 0px;
        background: #f7f7f7;
        height: 20px;
        line-height: 20px;
        padding: 0px;
        font-size:  12px;
        caret-color:#FD666A;
    }
    .searchBar{
        width: 345px;
        height: 38px;
        /*padding: 7px 15px;*/
        background: #F7F7F7;
        border-radius: 19px;
        line-height: 38px;
        margin-left: 15px;
    }
    .cityBtnDiv{
        float: left;
        height: 25px;
        width: 70px;
        line-height: 25px;
        border-right: 1PX solid #D8D8D8;
        margin-top: 6px;
        margin-left: 5px;
        .cityBtn{
            font-family: PingFangSC-Medium;
            font-size: 16px;
            color: #333333;
            letter-spacing: 0;
            float: left;
            margin-left: 5px;
            width: 50px;
            overflow-x: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
        img{
            width: 6px;
            height: 4px;
            float: right;
            margin-top: 11px;
            margin-right: 3px;
        }
    }
    .mapDiv{
        margin-top: 7px;
    }
    .address-list{
        overflow-y: scroll;
        ul{
            li{
                margin-top: 15px;
                display: flex;
                .addressRightInfo{
                    padding-left: 8px;
                }
            }
        }
    }
    .addressName{
        font-family: PingFangSC-Medium;
        font-size: 16px;
        width: 300px;
        overflow-x: hidden;
        text-overflow: ellipsis;
        /*white-space: nowrap;*/
        padding: 0;
        /*height: 22.5px;*/
        line-height: 22.5px;
    }
    .addressDetail{
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #B2B2B2;
        text-align: left;
        /*height:20px;*/
        line-height: 20px;
    }
    .node-icon{
        padding-left: 15px;
        padding-top: 0;
        height: 22.5px;
        line-height: 22.5px;
        display: flex;
        align-items: center;
        .rollImg{
            width: 8px;
            height: 8px;
            margin-top: 1px;
        }
        .pointImg{
            margin-top: 1px;
            width: 8px;
            height: 12px;
        }
    }
    .rightInfo{
        height: 42px;
        padding-left: 15px;
        margin-top: 15px;
    }
    .titleBar{
        position: fixed;
        z-index: 162;
    }
    .advice-list{
        overflow-y: scroll;
        ul{
            li{
                display: flex;
                .rightInfo{
                    width: 300px;
                    .addressName{
                        font-family: PingFangSC-Medium;
                        font-size: 16px;
                        color: #333333;
                        letter-spacing: 0;
                        width: 300px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        line-clamp: 2;
                        -webkit-box-orient: vertical;
                    }
                    .addressDetail{
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        line-clamp: 2;
                        -webkit-box-orient: vertical;
                    }
                }
            }
        }
    }
    /*自定义搜索图标*/
    .searchIcon {
        width: auto;
        height: 13px;
        margin-left: 3px;
        margin-right: 2px;
    }
</style>